import Toggle from "./Toggle.js"

export default class Mute extends Toggle {

  constructor(props, context) {
    super(props, context)
    this.state.level = ""
    this.classList.add("amp-mute", true)
    this.state.altText = "MSG_MUTE"
  }

  get className() {
    return `${super.className} ${this.state.level}`
  }

  onChange() {
    const player = this.player
    player.muted = !player.muted
  }

  onvolumechange() {
    const volume = this.player.volume
    let level = ""

    if (volume == 0) {
      level = "amp-muted"
    } else if (volume < 0.5) {
      level = "amp-low"
    }

    this.setState({level})
  }

  render() {
    if (akamai.amp.Utils.isVolumeSettable() == false)
      return null

    return super.render()
  }
}
