import Component from "./Component.js"
import Utils from "./Utils.js"

export default class Ads extends Component {

  constructor(props, context) {
    super(props, context)
    this.classList.add("amp-ads", true)
    this.state.msg = ""
    this.state.time = ""
    this.bindHandlers(["onadbreakstart", "onadbreakend", "onadstarted", "onadtimeremaining"])
  }

  get container() {
    return this.refs.container
  }

  onready() {
    const ads = this.player.ads
    if (ads == null)
      return
    ads.addEventListener("breakstart", this.onadbreakstart)
    ads.addEventListener("breakend", this.onadbreakend)
    ads.addEventListener("started", this.onadstarted)
    ads.addEventListener("timeremaining", this.onadtimeremaining)
  }

  onmediachange() {
    this.onadbreakend()
  }

  onadbreakstart(event) {
    const adVO = event.detail
    this.setState({msg: "", time: ""})
  }

  onadbreakend(event) {
    this.setState({msg: "", time: ""})
    this.plugin.container.classList.update({"amp-ad-break": false})
    this.player.container.classList.remove("amp-ad-break")
  }

  onadstarted(event) {
    this.plugin.container.classList.update({"amp-ad-break": true})
    this.player.container.classList.add("amp-ad-break")

    const adVO = event.detail
    this.setState({time: adVO.duration, msg: `${this.getLocalizedString("MSG_AD")} ${adVO.position} ${this.getLocalizedString("MSG_OF")} ${adVO.totalAds}`})
  }

  onadtimeremaining(event) {
    this.setState({time: event.detail})
  }

  render() {
    if (this.player.ads == null)
      return null

    const time = (this.state.time != "") ? Utils.formatTimecode(Math.ceil(this.state.time)) : this.state.time

    return (
      <div className="amp-ads">
        <div ref="container" className="amp-ad-container" />
        <div className="amp-bar amp-ad-info">
          <div ref="count" className="amp-ad-component amp-ad-count">{this.state.msg}</div>
          <div ref="spacerLeft" className="amp-spacer" />
          <div ref="spacerRight" className="amp-spacer" />
          <div ref="time" className="amp-ad-component amp-ad-time">{time}</div>
        </div>
      </div>
    )
  }
}
